* {
  box-sizing: border-box;
}

h1, h2, h3, a, li, ul, p, body {
  margin: 0;
  padding: 0;
}

.page {
  font-family: Arial, Helvetica, sans-serif;
}

.page__gallery {
  background: url("https://picsum.photos/800/800") center / cover no-repeat;
  min-height: calc(100vh - 200px);
  padding: 40px;
}

.page__buttons {
  text-align: center;
  line-height: 80px;
}

.standard-block {
  color: #fff;
  background-color: #333;
}

.standard-block__title {
  text-align: center;
}

.standard-block__title--header {
  line-height: 80px;
}

.standard-block__title--footer {
  line-height: 40px;
}

.item {
  float: left;
  width: 33%;
  padding: 10px;
  transition: transform 1s;
}

.item:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.item__pic {
  object-fit: cover;
  width: 100%;
  height: 300px;
}

.item__text {
  color: #fff;
  text-align: center;
  background-color: #00000080;
  padding: 10px 0;
}

.buttons__button {
  color: #fff;
  background-color: #333;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .8s;
}

.buttons__button:first-child {
  margin-right: 10px;
}

.buttons__button:hover {
  cursor: pointer;
  background-color: #33333380;
}
